<script>
import { mapActions } from 'vuex';
export default {
	Name: 'EditDesignation',
	data() {
		return {
			editableDesignation: {
				designation: this.$store.state.designation,
				edit: false
			},
		}
	},
	methods: {
		...mapActions({
			saveDesignation: 'updateDesignation'
		}),
		updateDesignation() {
			this.saveDesignation(this.editableDesignation.designation);
			this.$emit('notification', 'Changes saved locally!');
		},
		updateEditStatus() {
			this.editableDesignation.edit = true;
			setTimeout(() => {
				this.$refs.editableDesignation.focus();
			},100)
		}
	}
}
</script>

<style scoped>
.designation-form {
	padding: 15vh 2rem;
}

input[type=text], input[type=password], textarea {
