	background: none !important;
	opacity: 0.9;
	width: 100%;
	font-size: 2em;
	font-weight: 700;
	color: white;
	margin-bottom: 1rem;
}
input[type=text]:focus, input[type=password]:focus, textarea:focus {
	background-color: #ddd;
	outline: none;
}

/* Title mock styles replicating original */
