	font-weight: 700;
	color: white;
	margin-top: 0;
	cursor: pointer;
}
.logo{ 
	margin-bottom: 30px;
	height: 10em;
}
@media screen and (max-width: 930px) {
	.designation,
	input[type=text], input[type=password], textarea{
		font-size: 1.5rem;
	}
