		font-size: 1rem;
	}
}
@media screen and (max-width: 650px) {
	#title {
		flex-direction: column-reverse;
		justify-content: center;
	}
	svg {
		height: 14vw;
	}
	.header {
		width: 70%;
	}
}
@media screen and (max-width: 466px) {
	svg {
		height: 18vw;
	}
}
@media screen and (max-width: 400px) {
	svg {
		height: 25vw;
